export function emit(Store) {  return (eventStr, cbProps) => {    if (eventStr === 'MOUNTED') Store.flags.IS_MOUNTED = true;    const reactions = Store.objects.reactions.filter(item => {      if (item.keepAlive) return item.eventStr === eventStr;      return item.eventStr === eventStr && !item.done;    });    reactions.forEach(reaction => {      reaction.done = true;      reaction.callback(cbProps);    });  };}