export function getDBMetadata({ instance, user_id }) {  return new Promise((resolve, reject) => {    const controlStore = instance.transaction('control').objectStore('control');    const requestControlRecords = controlStore.getAll();    requestControlRecords.onsuccess = () => {      const controlRecords = requestControlRecords.result || [];      const lastTimestamp = controlRecords.length > 0        ? controlRecords[controlRecords.length - 1].lastTimestamp        : 0;      const modelStore = instance.transaction('model').objectStore('model');      const requestModelKeys = modelStore.getAllKeys();      requestModelKeys.onsuccess = () => {        const localEntitiesIds = requestModelKeys.result || [];        resolve({          instance,          user_id,          lastTimestamp,          localEntitiesIds,        });      };      requestModelKeys.onerror = () => reject('Error getting Model Keys');    };    requestControlRecords.onerror = () => reject('Error getting Control Records');  });}