export function callServerMethod(siteUrl) {  return (method, args = {}) => {    // For each query string parameter sent, add it to the path    // const requestUrl = Object.keys(queryStringObject).reduce((acum, key, index) => {    //   const queryStr = `${key}=${queryStringObject[key]}`;    //   if (index === 0) return `${acum}?${queryStr}`;    //   return `${acum}&${queryStr}`;    // }, `${baseUrl}/${path}`);    return new Promise((resolve, reject) => {      // Form the http request as a JSON type      const xhr = new window.XMLHttpRequest();      xhr.open('POST', `${siteUrl}/api/methods`, true);      xhr.setRequestHeader('Content-type', 'application/json');      // For each header sent, add it to the request      // Object.keys(headers).forEach(key => {      //   xhr.setRequestHeader(key, headers[key]);      // });      // When the request comes back, handle the response      xhr.onreadystatechange = () => {        if (xhr.readyState === window.XMLHttpRequest.DONE) {          const statusCode = xhr.status;          if (![ 200, 201 ].includes(statusCode)) reject('XHR request failed');          const responseReturned = xhr.responseText;          resolve(JSON.parse(responseReturned));        }      };      // Send the payload as JSON      const payloadString = JSON.stringify({ method, args });      xhr.send(payloadString);    });  };}