export function notify(Store) {  return observable => {    // Notify computed    if (observable) {      Object.keys(Store.objects.computed).forEach(key => {        const { take } = Store.objects.computed[key];        if (take.includes(observable)) Store.methods.compute(key);      });    }    // Notify components    if (Store.flags.IS_MOUNTED) Store.methods.render();    // const findListener = Store.objects.listeners.find(item => item.observable === observable);    // if (findListener && findListener.components) {    //   findListener.components.forEach(item => {    //     const { registerNumber } = item;    //     const componentDefinition = Store.objects.components[registerNumber];    //     componentDefinition.definition._render();    //   });    // }  };}