import _ from '@jmaguirrei/belt';import { ui } from '/module/context';import { ForgotControl } from '../Forgot';const { Input } = ui.components;const { PasswordOptions } = ui.fragments;export default ui.hoc({  state(props, store) {    return {      language: store.get('language'),      name: store.get('forgot.name').split(' ')[0],      password: store.get('forgot.password'),      isPasswordVisible: store.get('forgot.password.isVisible'),    };  },  actions(props, store) {    return {      onInput: e => {        store.set({ 'forgot.password': e.target.value });      },      onClickRight: () => {        const { state, actions } = ForgotControl(store);        const enabled = state.rightEnabled;        if (enabled) actions.onClickRight();      },    };  },  styles: {    wrapper: `      display: flex;      flex-flow: column;      height: 100%;      justify-content: space-around;    `,    title: `      font-size: 28px;      padding: 20px 20px 10px 20px;    `,    subtitle: `      font-size: 24px;      padding: 10px 20px 20px 20px;    `,    'input-wrapper': `      display: flex;      flex-flow: column;    `,  },  render({ state, styles, actions }) {    const { language, name, password } = state;    return ui.html`      <div style=${styles.wrapper}>        <div>          <div style=${styles.title}>            ${_.get({              en: `Don't worry ${name}!`,              es: `¡No te preocupes ${name}!`,            }, language)}          </div>          <div style=${styles.subtitle}>            ${_.get({              en: 'Just type a new password',              es: 'Sólo ingresa una nueva contraseña',            }, language)}          </div>        </div>        <div style=${styles['input-wrapper']}>          ${Input({            type: state.isPasswordVisible ? 'text' : 'password',            placeholder: '********',            value: password,            onInput: actions.onInput,            onClickRight: actions.onClickRight,          })}          ${PasswordOptions({ page: 'forgot' })}        </div>      </div>    `;  }});