/* ------------------------------------------------------------------------------------------------===== SIGNIN WORFLOW =====0: EMAIL    isRegistered ?    - false: error    - true: continue1: PASSWORD    - ko: error    - ok: => Dashboard!    - forgot => ===== FORGOT WORFLOW =====-------------------------------------------------------------------------------------------------*/import _ from '@jmaguirrei/belt';import { ui } from '/module/context';import Email from './<>/Email';import Password from './<>/Password';/* --------------------------------------------------------------------------------------------- */const { Carrousel, Navigation } = ui.components;const { Header, Tabs } = ui.fragments;const screens = [ Email, Password ];const numSteps = screens.length;/* --------------------------------------------------------------------------------------------- */export const SigninControl = store => {  const language = store.get('language');  const currentStep = store.get('signin.currentStep');  const currentCheck = _.get({    0: store.check('email', store.get('signin.email')),    1: store.check('password', store.get('signin.password')),  }, currentStep, {});  const checkResult = _.get(currentCheck, 'result', false);  const alert = store.alerts.find(item => item.isVisible);  return {    state: {      currentStep,      leftEnabled: currentStep > 0,      rightEnabled: checkResult,      checkText: _.get(currentCheck, `message.${language}`, ''),      alertText: _.get(alert, `message.${language}`, ''),    },    actions: {      onClickLeft: () => {        // const currentStep = store.get('signin.currentStep');        if (currentStep > 0) store.set({ 'signin.currentStep': currentStep - 1 });      },      onClickRight: () => {        // const currentStep = store.get('signin.currentStep');        if (currentStep === 0) store.call('signinEmail');        if (currentStep === 1) store.call('signinPassword');      },    },  };};/* --------------------------------------------------------------------------------------------- */export default ui.hoc({  state(props, store) {    return SigninControl(store).state;  },  actions(props, store) {    return SigninControl(store).actions;  },  classes: {    container: `      display: flex;      flex-flow: column;      justify-content: space-around;      height: 100%;      position: relative;    `,  },  render({ state, actions, classes }) {    const { currentStep, leftEnabled, rightEnabled, checkText, alertText } = state;    const { onClickLeft, onClickRight } = actions;    return ui.html`      <div id='sign-in' class=${classes.container}>        ${Header({ numSteps, currentStep })}        ${Carrousel({ currentStep, screens })}        ${Navigation({          currentPage: 'signin',          onClickLeft,          onClickRight,          leftEnabled,          rightEnabled,          text: alertText || checkText,          color: alertText ? ui.lib.Colors.RED_WARNING : '',        })}        ${Tabs()}      </div>    `;  }});