import _ from '@jmaguirrei/belt';import { ui } from '/module/context';const { Token, NumKeyboard } = ui.components;export default ui.hoc({  state(props, store) {    return {      language: store.get('language'),      tokenDigits: store.get('signup.tokenDigits'),    };  },  actions(props, store) {    return {      onPressNum: digit => {        if (store.get('signup.tokenDigits').length < 6) {          store.set({ 'signup.tokenDigits': value => value.concat(String(digit)) });        }      },      onPressDel: () => {        if (store.get('signup.tokenDigits').length > 0) {          store.set({ 'signup.tokenDigits': value => value.slice(0, -1) });        }      },    };  },  styles: {    wrapper: `      display: flex;      flex-flow: column;      height: 100%;      justify-content: space-around;    `,    title: `      font-size: 28px;      padding: 20px 20px 10px 20px;    `,    subtitle: `      font-size: 24px;      padding: 10px 20px 20px 20px;    `,  },  render({ state, styles, actions }) {    const { language, tokenDigits } = state;    const { onPressNum, onPressDel } = actions;    return ui.html`      <div style=${styles.wrapper}>        <div>          <div style=${styles.title}>            ${_.get({              en: 'Excelent! Last step:',              es: '¡Excelente! Último paso:',            }, language)}          </div>          <div style=${styles.subtitle}>            ${_.get({              en: 'Enter the code received by email',              es: 'Ingresa el código recibido por email',            }, language)}          </div>        </div>        ${Token({ tokenDigits })}        ${NumKeyboard({ onPressNum, onPressDel })}      </div>    `;  }});