export function toggle(Store) {  return (observable, cb) => {    const currentValue = Store.objects.observables[observable];    if (typeof currentValue === 'undefined') {      console.warn(`Observable [${observable}] does not exists.`);    } else if (typeof currentValue !== 'boolean') {      console.warn(`Observable [${observable}] is not a boolean.`);    } else {      Store.methods.set({ [observable]: !currentValue });      if (cb) cb(!currentValue);    }  };}