export function subscribe(Store) {  return (registerNumber, observable) => {    const findListener = Store.objects.listeners.find(item => item.observable === observable);    if (!findListener) return console.log(`Listener not found on observable ${observable}`);    const currentListeners = findListener.components;    const isSubscribed = currentListeners.find(item => item.registerNumber === registerNumber);    if (!isSubscribed) currentListeners.push({ registerNumber });  };}