export function alertOn(Store, alerts) {  return (obj = {}) => {    const rand1000 = Math.floor(1000 * Math.random());    const timestamp = Date.now();    const _id = `${timestamp}--${rand1000}`;    const message = obj.name ? alerts[obj.name] : obj.name;    Store.objects.alerts.push({      _id,      ...obj,      message,      timestamp,      isVisible: true,    });    // Maybe later we are going to notify only the exact component using alerts    Store.methods.notify(null);    if (obj.timeout) {      setTimeout(() => {        Store.methods.alertOff({ _id });      }, obj.timeout);    }  };}