export function createProcesses(Store, actions) {  if (!actions) return () => undefined;  const processes = Object.keys(actions).reduce((acum, key) => {    const singleAction = actions[key];    /*      We dont want to pass the whole Store to the action, just observables      So, the actions consumes the Store like:      const { language } = Store.observables;    */    const { observables } = Store.objects;    return {      ...acum,      [key]: args => {        Store.process.addToQueue({          name: key,          ...singleAction({ observables })(args),        });      },    };  }, {});  return (name, args) => processes[name](args);}