export function updateDBControl({ instance, counter, newTimestamp }) {  return new Promise(resolve => {    const controlStore = instance.transaction([ 'control' ], 'readwrite').objectStore('control');    const putRequest = controlStore.put({      _id: newTimestamp,      lastTimestamp: newTimestamp,      counter,    });    putRequest.onsuccess = () => {      resolve({ instance });    };  });}